import { useContentfulData } from "../../Content/useContentfulData";
import LoadingData from "../../Assets/Loading.jsx";

const Education = () => {
  const [educationpageData, loadingeducationpageData] = useContentfulData(
    "educationpage"
  );
  // const [projectPageData, loadingprojectPageData] = useContentfulData("experiencepage");

  if (loadingeducationpageData) {
    return <LoadingData />;
  }

  return (
    <div className=" education-section" id="education">
      <header
        className="App-header container padding-50 xheight-100 section-height vertical-center"
        id="education"
      >
        <div className="heading-behind ">Education</div>

        <Typography variant="h2" className="heading">
          Education{" "}
        </Typography>

        {educationpageData.items.map((educationpage) => {
          // console.log(experiencepage);
          for (var i = 0; i < educationpage.fields.education.length; i++) {
            var theeducation = educationpage.fields.education[i];
            // console.log(theeducation);
            // console.log(theexperience.fields.accomplishments[0]);

            // experiencepage.fields.project.forEach(function (item, index, array) {
            //   console.log(item, index);
            // });

            // experiencepage.fields.project.forEach(function (item, index, array) {
            //   console.log(item, index);
            // });

            return (
              <div className="experience">
                <Typography variant="h3" className="xabout-text">
                  <div className="content ">
                    <Grid
                      container
                      // spacing={9}
                      direction="row"
                      justify="center"
                      alignItems="stretch"
                    >
                      {educationpage.fields.education.map((education) => (
                        <Grid xs={12} sm={6} className="card infos">
                          <div className="xinfos">
                            <Grid
                              container
                              direction="row"
                              justify="center"
                              alignItems="stretch"
                              className=" top"
                            >
                              <Grid xs={11}>
                                <Typography variant="h4" className="title ">
                                  {education.fields.degree}
                                </Typography>
                                <Typography className="company">
                                  {education.fields.school}
                                </Typography>
                              </Grid>
                              <Grid xs={1}>
                                <Typography variant="h4" className="date">
                                  {education.fields.year}
                                </Typography>
                              </Grid>
                            </Grid>
                            <br />

                            <Typography
                              variant="h4"
                              className="description"
                            ></Typography>
                          </div>

                          <ul className="accomplishment">
                            {education.fields.accomplishment.content.map(
                              (accomplishments) => (
                                <li>{accomplishments.content[0].value}</li>
                              )
                            )}
                          </ul>
                        </Grid>
                      ))}
                    </Grid>
                  </div>
                </Typography>
              </div>
            );
          }
        })}
      </header>

      <div className="custom-shape-divider-bottom-16070489245">
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M1200 120L0 16.48 0 0 1200 0 1200 120z"
            className="shape-fill"
          ></path>
        </svg>
      </div>
    </div>
  );
};

export default Education;

